(function () {
	"use strict";
	// get Qualys stock percent change from Yahoo API and display it in page
	var url = "//query.yahooapis.com/v1/public/yql?q=select%20*%20from%20yahoo.finance.quotes%20where%20symbol%20in%20(%22QLYS%22)&format=json&env=store%3A%2F%2Fdatatables.org%2Falltableswithkeys&callback=";

	$(document).ready(function() {
		$.get(url, function(data) {
			var price = data.query.results.quote.LastTradePriceOnly;
			var changeInPercent = data.query.results.quote.ChangeinPercent;
			var prefix = changeInPercent.substring(0,1);
			if (prefix === "+") {
				$("div.stockWidget").addClass("up");
			} else {
				$("div.stockWidget").addClass("down");
			}
			$("div.stockWidget").before("$" + price + " ").html(changeInPercent);
		});
	});
}($));
